  bottom: 0;
}
.right-pad-box {
  position: absolute;
  top: 0;
  left: 100%;
  right: -30%;
  bottom: 0;
  border: 1px solid #3F6AA1;
  border-radius: 5px;
  background-color: #38648D;
  -webkit-transition: all 0.5s ease 0s;
  transition: all 0.5s ease 0s;
}
